/**
 * -------------------------------------------------------------------------------
 * ( 01 ) - Content Preview
 * -------------------------------------------------------------------------------
 *
 *  This part contains all content sections in content preview
 *
 * ------
 *
 *  Content is divided to the following sections:
 *
 *    |
 *    |-->  [ 01 ] - Intro Banner
 *    |-->  [ 02 ] - 
 *
**/


#content-preview {
  position: relative;
  // overflow: hidden;
  line-height: $line-height-content;
  background-color: #fff;
  z-index: 5; // Important for icons animations
}

#content-preview-wrap {
  position: relative;
}


.center-horizontally {
    display: flex;
    flex-direction: column;
    align-items: center;
}

/*  [ 01 ] - Intro Banner
=========================== */

#intro-banner {
    text-align: center;
    
    .section-content {
        padding: 150px 0;
    }

    .logo-banner {
        margin-bottom: 50px;
        max-width: 200px;
    }

    h1 {
        // font-weight: 400;
    }

    p {
        font-size: 20px;
        opacity: 0.6;
        color: #fff;
    }
}



/*  [ 02 ] - Demo Pages
========================= */

#demo-pages {
    .section-content {
      padding: 130px 100px;

        @media (max-width: 992px) {
          & {
            padding-left: 50px;
            padding-right: 50px;
          }
        } // < 992px width
    }

    h2 {
      margin-bottom: 50px;
    }
}

/* List Items */
.list-boxes {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 -15px -30px;

  &.boxes-2 {
    > li { width: 50%; }
  }

  &.boxes-3 {
    > li { width: 33.33333%; }
  }

  &.boxes-4 {
    > li { width: 25%; }

    @media (max-width: 1400px) {
      > li {
        width: 33.33333%;
      }
    } // < 1400px width

    @media (max-width: 1200px) {
      > li {
        width: 50%;
      }
    } // < 1200px width
  }

  > li {
    padding: 0 15px;
    margin-bottom: 30px;
  }
}

@media (max-width: 992px) {
  .list-boxes {
    &.boxes-2 {
      > li {
        width: 100%;
      }
    }
  }
} // < 1200px width

@media (max-width: 992px) {
  .list-boxes {
    &.boxes-3 {
      > li { width: 50%; }
      
      &.mq-992 {
        > li {

        }
      }
    }
  }
} // < 992px width

@media (max-width: 768px) {
  .list-boxes {
    &.boxes-3,
    &.boxes-4 {
      > li { width: 100%; }
      
      &.mq-768 {
        > li {
          width: 100%;
        }
      }
    }
  }
} // < 768px width


/* List Demo Pags */
.list-demo-pages {
    margin-top: 60px;
    margin-bottom: -50px;
    margin: 60px -30px -70px;

    &.default {
        margin-bottom: 100px;
        padding-bottom: 30px;
        border-bottom: 1px solid #eee;
    }


  &:last-child {
    padding-bottom: 0;
    border-bottom: none;
  }

    li {
        padding: 0 30px;
        margin-bottom: 70px;
        
        & + li {

        }
    }
}


/* Box Demo Page */
.box-demo-page {
  position: relative;
  z-index: 0;
  transition: all 0.2s;

  &.box-preview {
    .box-img {
      overflow: hidden;
      margin: auto;
      // padding: 30px;
      height: 350px;
      // border-radius: 4px;
      box-shadow: 0 8px 15px 5px rgba(#000, 0.1);
      background-color: #fff;

      @media (max-width: 1440px) {
        & {
          height: 250px;
        }
      } // < 1440px width
      
      > a {
        display: flex;
        align-items: flex-start;
        justify-content: center;
        width: 100%;
        height: 100%;
        transition: all 0.2s;

        img {
        //   max-height: 100%;
        //   width: auto;
        }
      }

      .overlay {
        color: #bbb; // content color inside overlay
        background-color: rgba(#fff, 0);

        .overlay-inner {
          align-items: center;
          justify-content: center;
          transition-duration: 0.3s;
        }

        // overlay & overlay-inner default visibility
        &,
        .overlay-inner {
          opacity: 0;
          visibility: hidden;
        }
      }

      // hover box img
      &:hover {
        .overlay {
          &,
          .overlay-inner {

          }
        }
      }
    }

    // hover box preview
    &:hover {
      .box-img {
        > .bg-element {
          opacity: 0.6;
        }
      }

      .overlay {
          background-color: rgba(#000, 0.7);
          
        &,
        .overlay-inner {
          opacity: 1;
          visibility: visible;
        }

        .overlay-inner {
          transition-delay: 0.3s;
        }
      }

      .box-content {

      }
    }

    .box-content {
      display: flex;
      flex-direction: column;
      padding: 30px 30px 0;
      text-align: center;
      // background-color: #fff;

      h4 {
        margin-bottom: 0;
        a {
          color: $dark-color;

          &:hover {
            color: $theme-color-1;
          }
        }
      }
    }
  }
}

/* Box Preview */
.box-preview {
  width: 100%;
  position: relative;
  top: 0;
  transition: all 0.3s ease;

  &:hover {
    .box-img {  
      .overlay {

        // overlay & overlay-inner hover state visibility
        &,
        .overlay-inner {
          opacity: 1;
          visibility: visible;          
        }

        // overlay-inner show delay when hidden
        .overlay-inner {
          transition-delay: 0.25s;
        }
      }
    }
  }

  .box-img {
    position: relative;
    border-radius: 0;
    transition: all 0.3s ease;

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background-color: rgba(#000,0.75);
      transition: all 0.3s ease;

      // overlay & overlay-inner default state visibility
      &,
      .overlay-inner {
        opacity: 0;
        visibility: hidden;
      }

      .overlay-inner {
        transition: all 0.3s ease;
      }
    }
  }

  .box-content {
    padding: 45px 25px;
    position: relative;
  }
}


/* Footer */
#footer-bar-1.footer-bar .fb-row {
  justify-content: center;
  border-top: none;
  color: #fff;
}

.copyrights-message {
  margin-right: 0;
  
  a {
    font-weight: 400;
  }
}


